/*** Top Navigation ***/
/****************************************************************/
#access {
 /* For browsers that do not support gradients */
    display: block;
    width: 100%;
    position: relative;
    z-index: 9999;
}

#access, .sf-menu, .sf-menu a {
    height: 50px;
}

.sticky-wrapper {
    background: transparent;
    position: fixed;
    width: 100%;
    top: 0;
}

.stuck {
    background: #fff;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 999999;
}

.sticky.scrolled {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.27);
    -moz-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.27);
    -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.27);
}

.sf-menu a {
    background: none;
    color: #7d8589;
    font-family: 'Quicksand', Helvetica, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
    display: block;
    line-height: 50px;
    position: relative;
    padding: 0;
    text-decoration: none;
    zoom: 1;
    margin: 0 1.25rem;
    height: 50px;
}

.sf-menu a:hover {
    color: #333;
    background-color: transparent;
    text-decoration: none;
    border-bottom: 1px solid #D3DFB8;
}

.hover {
    color: #333 !important;
    background-color: transparent !important;
    text-decoration: none;
    border-bottom: 1px solid #D3DFB8;
}

