    text-indent: -9999px;
    white-space: nowrap;
    overflow: hidden;
}

#access #searchform input[type="submit"]:hover {
    opacity: 0.8;
    cursor: pointer;
}

.sf-menu, .sf-menu * {
    margin: 0;
    padding: 0;
    list-style: none;
}

.sf-menu li {
    position: relative;
}

.sf-menu ul {
    position: absolute;
    display: none;
    left: 0;
    z-index: 99;
    top: 66px;
}

.stuck .sf-menu ul {
    position: absolute;
    display: none;
    top: 66px;
    left: 0;
    z-index: 99;
}

.stuck .sf-menu ul ul {
    position: absolute;
    display: none;
    top: 0 !important;
    left: 0;
    z-index: 99;
    top: 55px;
}

.sf-menu > li {
    float: none;
    padding-bottom: 3rem;
 /* For Hover */
    display: inline-block;
    margin-left: 0;
}

.sf-menu > li:hover {
    cursor: pointer;
}

.sf-menu li:hover > ul, .sf-menu li.sfHover > ul {
    display: block;
}

.sf-menu ul ul {
    background: #7D8589;
    top: 0;
    left: 100% !important;
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
}

.sf-menu ul ul ul {
    background: #555;
}

.sf-menu ul ul ul li a:hover {
    background: #fff !important;
    color: #111 !important;
}

/*** point all arrows down ***/
.sf-arrows.sf-navbar ul .sf-with-ul:after {
    margin-top: -3px;
    margin-right: 0;
    border-color: transparent;
    border-top-color: #dFeEFF;
 /* edit this to suit design (no rgba in IE8) */
    border-top-color: rgba(255, 255, 255, 0.5);
}

.sf-arrows.sf-navbar ul > li > .sf-with-ul:focus:after, .sf-arrows.sf-navbar ul > li:hover > .sf-with-ul:after, .sf-arrows.sf-navbar ul > .sfHover > .sf-with-ul:after {
    border-color: transparent;
    border-top-color: white;
}

.sf-menu {
    margin: 0 auto;
    width: 100%;
    text-align: center;
    margin-left: 0%;
}

.sf-menu ul {
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.3);
    min-width: 12em;
 /* allow long menu items to determine submenu width */
    *width: 12em;
 /* no auto sub width for IE7, see white-space comment below */
}

.sf-menu ul ul li a {
    color: #fff !important;
}

.sf-menu ul ul li a:hover {
    color: #fff !important;
    background: #333 !important;
}

.sf-menu > li:first-child > a {
    border-left: 0;
}

.sf-menu li {
    background: none;
    white-space: nowrap;
 /* no need for Supersubs plugin */
    *white-space: normal;
 /* ...unless you support IE7 (let it wrap) */
    height: auto;
}

.sf-menu ul li a, .sf-menu ul li:first-child a {
    background: none;
}

.sf-menu li li a:hover {
    background: #D3DFB8 !important;
    color: #fff !important;
    border: 0 !important;
}

.sf-menu li:hover, .sf-menu li.sfHover, .sf-menu li:hover a, .sf-menu li.sfHover a {
 /* only transition out, not in */
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}

/*** arrows (for all except IE7) **/
.sf-arrows .sf-with-ul {
    padding-right: 2.5em;
    *padding-right: 1em;
 /* no CSS arrows for IE7 (lack pseudo-elements) */
}

/* styling for both css and generated arrows */
.sf-arrows .sf-with-ul:after {
    content: '';
    position: absolute;
    top: 50%;
    right: 1em;
    margin-top: -3px;
    height: 0;
    width: 0;
 /* order of following 3 rules important for fallbacks to work */
    border: 5px solid transparent;
    border-top-color: #dFeEFF;
 /* edit this to suit design (no rgba in IE8) */
    border-top-color: rgba(255, 255, 255, 0.5);
}

.sf-arrows > li > .sf-with-ul:focus:after, .sf-arrows > li:hover > .sf-with-ul:after, .sf-arrows > .sfHover > .sf-with-ul:after {
    border-top-color: white;
 /* IE8 fallback colour */
}

/* styling for right-facing arrows */
.sf-arrows ul .sf-with-ul:after {
